.coin-input {
  margin-bottom: 10px;
}

.coin-input label {
  font-size: 18px;
  margin-right: 10px;
}

.coin-input input {
  font-size: 18px;
  padding: 5px;
  width: 100px;
}

.coin-game {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: sans-serif;
  margin: 20px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button[type="submit"] {
  font-size: 18px;
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #0066cc;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0059b3;
}

.error {
  color: red;
  margin-top: 10px;
}

.success {
  color: green;
  margin-top: 10px;
}
